const initialState = {
    check: false
  }
 export const check = (state = false, action) => {
      switch (action.type){
          case 'CHECK_BOX':
          console.warn("action done")
              return {
                
                state: !state,
                id: action.id,
                text: action.text,
                ...state,
              }            
          default:
              return state
      }
  }

  export default check